export default {
    name: "myMessage",
  data(){
      return{
        api: this.GLOBAL.baseURL,
        clickIndex:1,
        zanList:[],
        userId:'',
        index:1
      }
  },
  components:{

  },
  methods:{
    goBack(){
      this.$router.go(-1)
    },
    choose(val){
    this.clickIndex = val
      if(val == 1){
        this.getZan(1)
      }else{
        this.getReplay(1)
      }
    },
    getUser(){
      this.$http.post(this.api + '/home/User/get_user',{
        token: localStorage.getItem('token')
      })
        .then(res=>{
          if(res.data.code == 1){
            this.userId = res.data.data[0].user_id
            this.getZan(1)
          }else if(res.data.code == 3){
            this.$http.post(this.api + '/home/index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.getUser()
              })
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    },
    getZan(val){
      this.$http.post(this.api + '/MyPraise',{
        token: localStorage.getItem('token'),
        user_id: this.userId,
        page: val
      })
        .then(res=>{
          if(res.data.code == 1){
            this.zanList = res.data.data.data
          }else if(res.data.code == 3){
            this.$http.post(this.api + '/home/index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.getZan(val)
              })
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    },
    getReplay(val){
      this.$http.post(this.api + '/msgreply',{
        token: localStorage.getItem('token'),
        page: val
      })
        .then(res=>{
          if(res.data.code == 1){
            console.log(res)
          }else if(res.data.code == 3){
            this.$http.post(this.api + '/home/index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.getReplay(val)
              })
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    }
  },
  mounted() {
      this.getUser()
    this.getReplay()
  }
}



